export default {
  computed: {
    userData() {
      return this.$store.getters['modules/user/user']
    },
    steps() {
      if (!this.$store.getters['modules/user/public'].info) return 1
      if (!this.$store.getters['modules/user/stripeDone']) return 2
      return 3
    },
  },
}
